.SettingsPage {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  padding: 50px 0;
  text-align: center;
  background-image: linear-gradient(
      105deg,
      rgba(25, 25, 25, 0.4) 0%,
      rgba(0, 0, 0, 0.4) 100%
    ),
    url(../../assets/showcase.jpg);
  background-size: cover;
  background-position: bottom;
}

.SettingsPage__heading {
  margin-bottom: 1rem;
  color: white;
}

.SettingsPage__form {
  width: 90%;
  max-width: 800px;
  margin: 0 auto;
  padding: 2rem;
  border: 2px solid black;
  margin-bottom: 1rem;
  background-color: aliceblue;
  align-items: center;
  text-align: center;
}

.SettingsPage__input {
  max-width: 600px;
  margin: auto;
  margin-bottom: 1rem;
}
